<template>

<div>

    <h2>Registro de Usuario!</h2>
    
    <hr>

    <div class="container">

        <div v-if="error != undefined">
            <p class="text-light pt-2 pb-2  bg-danger rounded-3">{{error}}</p>
        </div>
        
        <p>Nome:</p>
        <input class="form-control" v-model="name" type="text" placeholder="Nome do Usuario">

        <p>E-mail:</p>
        <input class="form-control" v-model="email" type="email" placeholder="email@email.com">

        <p>Senha:</p>
        <input class="form-control" v-model="password" type="password" placeholder="*****">

        <hr>

        <div class="d-grid mt-2">
            <button @click="register" class="btn btn-success">Cadastrar</button>
        </div>

        <router-link to="/login" class="align">Ja está cadastrado?</router-link>

    </div>

</div>
  
</template>

<script>

import axios from "axios"


export default {

    data() {

        return {

            name: '',
            email: '',
            password: '',
            error: undefined,

        }

    },

    methods: {

        register() {

        axios.post("https://projeto-api-auth-7h57.vercel.app/user",{

            name: this.name,
            email: this.email,
            password: this.password

        }).then(res => {

            console.log(res)
            this.$router.push({name: 'home'})


        }).catch(err => {

            var msgErro = err.response.data.err
            this.error = msgErro

        })


    }

    }

}
</script>

<style scoped>
.align{

    display: flex;
    justify-content: end;
    

}
</style>>
