<template>

<div>

    <h2>Login</h2>
    
    <hr>

    <div class="container">

        <div v-if="error != undefined">
            <p class="text-light pt-2 pb-2  bg-danger rounded-3">{{error}}</p>
        </div>

        <p>E-mail:</p>
        <input class="form-control" v-model="email" type="email" placeholder="email@email.com">

        <p>Senha:</p>
        <input class="form-control" v-model="password" type="password" placeholder="*****">

        <hr>

        <div class="d-grid mt-2">
            <button @click="login" class="btn btn-success">Logar</button>
        </div>

        <router-link to="/register" class="align">Ainda não está cadastrado?</router-link>

    </div>  

</div>
  
</template>

<script>

import axios from "axios"


export default {

    data() {

        return {

            email: '',
            password: '',
            error: undefined,

        }

    },

    methods: {

        login() {

        axios.post("https://projeto-api-auth-7h57.vercel.app/login",{

            email: this.email,
            password: this.password

        }).then(res => {

            console.log(res)
            localStorage.setItem('token', res.data.token)
            this.$router.push({name: "Users"})



        }).catch(err => {

            console.log(err)
            var msgErro = err.response.data.err
            this.error = msgErro

        })


        }
}

}
</script>

<style scoped>

.align{

    display: flex;
    justify-content: end;
    

}
</style>>
