<template>

    <div>
        <h1>Painel Adm!</h1>  

        <div>

            <table class="table table-bordered">

                <thead>

                    <tr>
                        <th>Nome</th>
                        <th>E-mail</th>
                        <th>Cargo</th>
                        <th>Acoes</th>
                    </tr>

                </thead>

                <tbody>
                    <tr v-for="user in users" :key="user.id">

                        <td>{{user.name}}</td>
                        <td>{{user.email}}</td>
                        <td>{{processRole(user.role)}}</td>
                        <td>
                            <router-link :to="{name: 'UserEdit', params: {id: user.id} }">
                            <button 
                            class="btn btn-success"                         
                            >Editar</button>
                            </router-link>

                            <button
                             @click="showModal(user.id)"
                             class="btn btn-danger"
                             data-bs-toggle="modal"
                             data-bs-target="#modalId"
                             >Deletar</button>
                        </td>



                    </tr>
                </tbody>

            </table>
            
        </div>


        
        <!-- Modal -->
        <div
            class="modal fade"
            id="modalId"
            tabindex="-1"
            role="dialog"
            aria-labelledby="modalTitleId"
            aria-hidden="true"
        >
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="modalTitleId">
                            Voce Quer Realmente Deletar este usuario
                        </h5>
                        <button
                            id="close"
                            type="button"
                            class="btn-close"
                            data-bs-dismiss="modal"
                            aria-label="Close"
                        ></button>
                    </div>
                    <div class="modal-body">
                        <div class="container-fluid">Apos Confirmar Sera Apagado E Nao Tera como Recuperar</div>
                    </div>
                    <div class="modal-footer">
                        <button
                            type="button"
                            class="btn btn-secondary"
                            data-bs-dismiss="modal"
                        >
                            Cancelar
                        </button>
                        <button @click="deleteUser" type="button" class="btn btn-primary">Sim, Quero Deletar</button>
                    </div>
                </div>
            </div>
        </div>
        
        


    </div>
  
</template>

<script>
import axios from 'axios'
export default {

    data() {

        return {

            users: [],
            deleteUserId: -1,
            req: {}
            


        }


    },

    created() {
      
    this.req = {

      headers: {

        Authorization: "Bearer " + localStorage.getItem('token')

      }

    }

    console.log(this.req)

        axios.get("https://projeto-api-auth-7h57.vercel.app/user", this.req).then(res => {


           this.users = res.data


        }).catch(err => {

            console.log(err)


        })
        console.log("OLA")


    },

    methods: {

        processRole: function(value) {

            if(value == 0) {

                return "Usuario Comum"

            }else if(value == 1) {


                return "Admin"

            }

            return

        },

        showModal: function(id){

            this.deleteUserId = id
            


        },

        deleteUser: function() {

            console.log(this.req)

            axios.delete("https://projeto-api-auth-7h57.vercel.app/user/"+this.deleteUserId, this.req).then(res => {

                console.log(res)
                document.querySelector('#close').click()
                this.users = this.users.filter(u => u.id != this.deleteUserId)
                


            }).catch(err => {

                console.log(err)


            })


        }


    }



    


}
</script>

<style scoped>

</style>