<template>

<div>

    <h2>Edicao de Usuario!</h2>
    
    <hr>

    <div class="container">

        <div v-if="error != undefined">
            <p class="text-light pt-2 pb-2  bg-danger rounded-3">{{error}}</p>
        </div>
        
        <p>Nome:</p>
        <input class="form-control" v-model="name" type="text" placeholder="Nome do Usuario">

        <p>E-mail:</p>
        <input class="form-control" v-model="email" type="email" placeholder="email@email.com">

        <hr>

        <div class="d-grid mt-2">
            <button @click="update" class="btn btn-success">Edit</button>
        </div>

    </div>

</div>
  
</template>

<script>

import axios from "axios"


export default {

    data() {

        return {

            name: '',
            email: '',
            id: -1,
            error: undefined,
            req: {}

        }

    },

    created() {

    this.req = {

      headers: {

        Authorization: "Bearer " + localStorage.getItem('token')

      }

    }

        axios.get("https://projeto-api-auth-7h57.vercel.app/user/"+this.$route.params.id,this.req).then(res => {

            this.name = res.data.name
            this.email = res.data.email
            this.id = res.data.id

        }).catch(err => {

            this.$router.push({name: "Users"})
            console.log(err)


        })

    },

    methods: {

        update() {

        axios.put("https://projeto-api-auth-7h57.vercel.app/user",{

            name: this.name,
            email: this.email,
            id: this.id


        }, this.req).then(res => {

            console.log(res)
            this.$router.push({name: 'Users'})


        }).catch(err => {

            var msgErro = err.response.data.err
            this.error = msgErro

        })


    }

    }

}
</script>

